<template>
	<div id="order-list">
		<public-header title="订单管理">
			<div slot="right" class="flex">
				<i class="icon icon-circle" @click="confirmDialog = true"></i>
				<template v-if="0&&util.isCloud() && util.isHasModule('FNScanner')">
					<i class="icon icon-scan  margin-10-l"></i>
				</template>
			</div>
		</public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="tabs flex-lr">
				<div class="tabs-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">
					<div class="mark">新</div>
					<div class="title">待处理</div>
				</div>
				<div class="tabs-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">
					<div class="mark">接</div>
					<div class="title">处理中</div>
				</div>
				<div class="tabs-item" :class="{active: filter.items.status == 3}" @click="onToggleStatus(3)">
					<div class="mark">待</div>
					<div class="title">待配送</div>
				</div>
				<div class="tabs-item" :class="{active: filter.items.status == 4}" @click="onToggleStatus(4)">
					<div class="mark">送</div>
					<div class="title">配送中</div>
				</div>
				<div class="tabs-item" :class="{active: filter.items.status == 5}" @click="onToggleStatus(5)">
					<div class="mark">完</div>
					<div class="title">已完成</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list 
						class="order-list padding-10 " 
						v-model="records.loading"
						:finished="records.finished"
						@load="onLoad"
						:offset="100"
						:immediate-check="false"
						v-if="!records.empty"
				>
					<div class="order-item bg-default font-14" v-for="(item, index) in records.data" :key="item.id">
						<div class="order-nt bg-gray flex-lr padding-10-tb padding-10-r ">
							<div class="number-time flex">
								<div class="number">{{item.serial_sn}}</div>
								<div class="c-info">{{item.delivery_time}}</div>
							</div>
							<div>{{item.order_status_cn}}</div>
						</div>
						<div class="cancel-count-down flex" v-if="item.customer_cancel_endtime > 0">
							<i class="icon icon-time"></i>本单请在
							<count-down :endTime="item.customer_cancel_endtime" class="c-danger margin-5-l margin-5-r" ></count-down>
							分钟后开始备餐
						</div>
						<!-- 取消原因 -->
						<div class="padding-10-lr padding-5-b">
							<div class="flex-lr padding-10-t">
								<div class="font-bold font-18">{{item.username}}({{item.sex}})</div>
								<!-- 收藏店铺 -->
								<div class="icon icon-locationfill" v-if="item.location_x && item.location_y" @click="util.jsLocation({lat: item.location_x, lng: item.location_y, address: item.address})"></div>
							</div>
							<div class="flex-lr c-info margin-5-t">
								<div class="icon icon-telephone" @click="util.jsTel(item.mobile)">
									<span class="margin-5-l">{{item.mobile}}</span>
								</div>
								<div v-if="item.order_type == 2" class="order-type bg-success">到店自提</div>
							</div>
							<div class="c-gray flex margin-5-t">
								<div class="accept-address ellipsis">{{item.address}}</div>
								<template v-if="item.distance > 0">
									<div class="padding-10-lr">|</div>
									<div>{{item.distance}}km</div>
								</template>
							</div>
						</div>
						<div class="divide">
							<div class="divide-line"></div>
						</div>
						<div class="goods-info ">
							<div class="flex-lr padding-10-lr padding-5-t padding-10-b">
								<div class="font-18 font-bold">商品</div>
								<div class="c-info flex goods-show" @click="onToggleGoodsShow(index)">
									<span class="margin-5-r">展开</span>
									<i class="icon icon-arrow-down"></i>
								</div>
							</div>
							<template v-if="item.showGoods">
								<van-row class="padding-10 ">
									<van-col span="3" class="c-danger">备注:</van-col>
									<van-col span="21" class="line-12">{{item.note ? item.note : '无'}}</van-col>
								</van-row>
								<div class="goods-list">
									<van-row class="goods-item" v-for="goodsItem in item.goods" :key="goodsItem.goods_id">
										<van-col span="16">{{goodsItem.goods_title}}</van-col>
										<van-col span="4" class="c-danger">x{{goodsItem.goods_num}}</van-col>
										<van-col span="4" class="text-right">¥{{goodsItem.goods_price}}</van-col>
									</van-row>
									<div class="other-fee flex-lr">
										<div>餐盒费</div>
										<div>¥{{item.box_price}}</div>
									</div>
									<div class="other-fee flex-lr">
										<div>包装费</div>
										<div>¥{{item.pack_fee}}</div>
									</div>
									<div class="other-fee flex-lr">
										<div>配送费</div>
										<div>¥{{item.delivery_fee}}</div>
									</div>
								</div>
								<div class="padding-10-lr">
									<div class="padding-10-tb van-hairline--bottom flex-lr font-16 ">
										<div class="font-bold">小计</div>
										<div class="font-bold">¥{{item.total_fee}}</div>
									</div>
								</div>
							</template>
							<div class="padding-10-lr padding-10-t padding-5-b">
								<div class="flex-lr ">
									<div class="font-18 font-bold">本单预计收入</div>
									<div class="font-16 c-danger font-bold">¥{{item.store_final_fee}}</div>
								</div>
								<div class="margin-10-t flex">
									<span class="c-gray margin-10-r">本单顾客实际支付:</span>
									<span class="c-danger">¥{{item.final_fee}}</span>
								</div>
							</div>
						</div>
						<div class="divide">
							<div class="divide-line"></div>
						</div>
						<div class=" action">
							<template v-if="item.status == 1">
								<div class="action-item bg-info" @click="onUpdateOrderStatus(index, 'handle')">确认</div>
								<div class="action-item bg-danger" @click="onUpdateOrderStatus(index, 'cancel')">取消</div>
							</template>
							<template v-else-if="item.status == 2 || item.status == 3">
								<template v-if="item.order_type == 1">
									<div class="action-item bg-info" @click="onUpdateOrderStatus(index, 'notify_deliveryer_collect')">通知配送</div>
									<template v-if="item.delivery_type == 1">
										<div class="action-item bg-info" @click="onUpdateOrderStatus(index, 'direct_deliveryer')">指定配送员</div>
										<div class="action-item bg-info" @click="onUpdateOrderStatus(index, 'delivery_ing')">设为配送中</div>
									</template>
									<div class="action-item bg-info" @click="onPushUU(index, item.id)" v-if="store.data.uupaotui && store.data.uupaotui.status == 1">UU配送</div>
								</template>
								<template v-else-if="item.order_type == 2">
									<div class="action-item bg-info" @click="onUpdateOrderStatus(index, 'end')">已自提</div>
								</template>
								<template v-else-if="item.order_type >= 3">
									<div class="action-item bg-info" @click="onUpdateOrderStatus(index, 'end')">完成</div>
								</template>
							</template>
							<template v-else-if="item.status == 4">
								<div class="action-item bg-info" v-if="item.order_type != 1" @click="onUpdateOrderStatus(index, 'end')">完成</div>
							</template>
							<template v-else-if="item.delivery_type == 1 && item.status > 1 && item.status < 5 && item.order_type == 1">
								<div class="action-item bg-info" @click="onUpdateOrderStatus(index, 'end')">完成</div>
							</template>
							<router-link tag="div" class="action-item" :to="util.getUrl({path: 'pages/order/detail', query: {id: item.id}})">详情</router-link>
						</div>
						<div class="divide">
							<div class="divide-line"></div>
						</div>
						<div class="order-info flex-lr padding-10 bg-gray">
							<div class="c-gray">
								<div class="flex">
									<div class="margin-10-r">下单时间:</div>
									<div>{{item.addtime_cn}}</div>
								</div>
								<div class="flex margin-10-t" v-clipboard:copy="item.ordersn" v-clipboard:success="onCopy">
									<div class="margin-10-r">单号:</div>
									<div>{{item.ordersn}}</div>
									<div class="margin-10-l c-default">复制</div>
								</div>
							</div>
							<div class="flex-tb-center" @click="onUpdateOrderStatus(index, 'print')">
								<div class="icon icon-print font-20"></div>
								<div class="margin-5-t">打印{{item.print_nums > 0 ? item.print_nums : ''}}</div>
							</div>
						</div>
						<div class="pay-status delivery-pay" v-if="item.is_pay == 1 && item.pay_type == 'delivery'"></div>
						<div class="pay-status have-pay" v-else-if="item.is_pay == 1 && item.pay_type !='delivery'"></div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>暂无数据</p>
				</div>
			</van-pull-refresh>
		</div>
		<van-dialog
			v-model="confirmDialog"
			title="核销码核销"
			show-cancel-button
			@confirm="onCodeConfirm"
		>
			<van-field
				v-model="code"
				placeholder="请输入6位核销码"
				input-align="center"
			/>
		</van-dialog>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import countDown from "@/components/countDown"
	import {getRecords, setData} from "../../controller/funcCommon"
	import {changeOrderStatus, confirmOrderByCode} from "./controller/order.js"
	export default {
		components: {
			publicHeader,
			publicFooter,
			countDown
		},
		data() {
			return {
				isRefresh: false,
				filter: {
					items: {
						status: '1'
					}
				},
				showPreLoading: true,
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				store: {},
				confirmDialog: false,
				code: '',
				menufooter: {}
			}
		},
		methods: {
			onUpdateOrderStatus(index, type) {
				var that = this;
				changeOrderStatus({
					vue: that,
					type: type,
					index: index,
					order: that.records.data[index],
				})
			},
			onLoad(force){
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'manage/order/takeout/list',
					recordsName: 'orders',
					data: {
						menufooter: 1
					},
					success: function(result) {
						that.store = result.store;
						that.isRefresh = false;
					}
				});
			},

			onPullDownRefresh() {
				this.onLoad(true);
			},

			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
				this.onLoad(true);
			},
			onToggleGoodsShow(index) {
				var that = this;
				this.$nextTick().then(() => {
					this.records.data[index].showGoods = !this.records.data[index].showGoods;
					this.records = Object.assign({}, this.records);
				})
			},
			onPushUU(index, orderId) {
				if(!orderId) {
					return false;
				}
				var that = this;
				setData({
					vue: that,
					url: 'manage/order/takeout/push_uupaotui',
					data: {
						id: orderId
					},
					success: function(res) {
						setData({
							vue: that,
							url: 'manage/order/takeout/push_uupaotui',
							confirm: res.tips,
							data: {
								id: res.id,
								push: 1
							},
						});
					},
				});
			},
			onCopy(e) {
				this.util.$toast('复制成功');
			},
			onCodeConfirm() {
				var that = this;
				var code = that.code;
				if(!code) {
					that.util.$toast('请输入核销码');
					return;
				}
				confirmOrderByCode({
					vue: that,
					code: code,
					confirm: '',
				})
			}
		},
		mounted() {
			var that = this;
			that.util.ilogin({
				success: function() {
					that.onLoad();
				}
			});
			this.util.imap();
			this.util.icloudapi();
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.status) {
				this.filter.items.status = this.query.status;
			}
		},
	}
</script>

<style>
	#order-list .content{
		padding-top: 77px;
		background-color:#F0EFF5;
	}
	#order-list .tabs{
		background-color: #33aafc;
		color: #fff;
		padding: 0 10px;
		position: fixed;
		z-index: 100;
		top: 46px;
		left: 0;
		right: 0;
	}
	#order-list .tabs .tabs-item{
		width: 20%;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: space-between;
		position: relative;
	}
	#order-list .tabs .tabs-item .mark{
		width: 30px;
		height: 30px;
		border-radius: 5px;
		background-color: #0C8DE4;
		text-align: center;
		line-height: 30px;
		margin: 10px auto;
		font-size: 16px;
	}
	#order-list .tabs .tabs-item .title{
		font-size: 12px;
		font-weight: 600;
		margin-bottom: 15px;
	}
	#order-list .tabs .tabs-item.active:after{
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 4px;
		background-color: #fff;
	}
	#order-list .tabs .tabs-item.active .mark{
		background-color: #fff;
		color: #33aafc;
	}
	#order-list .order-item{
		position: relative;
		margin-top: 10px;
	}
	#order-list .order-item:first-child{
		margin-top: 0px;
	}
	#order-list .order-item .number{
		background-color: #33aafc;
		color: #fff;
		min-width: 25px;
		height: 25px;
		font-size: 16px;
		font-weight: bold;
		text-align: center;
		line-height: 25px;
		padding: 0 7px;
		margin-right: 10px;
	}
	#order-list .order-item .icon-locationfill{
		width:28px;
		height:28px;
		border-radius:100%;
		border:1px solid #eee;
		text-align:center;
		line-height:29px;
		font-size:20px;
		color:#656565;
	}
	#order-list .order-item .accept-address{
		max-width: 80%;
	}
	#order-list .order-item .goods-item{
		background-color: #FFFCEB;
		color: #3E3D38;
		padding: 8px 10px;
	}
	#order-list .order-item .other-fee{
		color: #7a7a7a;
		padding: 8px 10px;
	}
	#order-list .order-item .action{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-end;
		padding: 5px 10px;
	}
	#order-list .order-item .action .action-item{
		padding:5px 8px;
		color:#333;
		background-color:#fff;
		text-align:center;
		border-radius:4px;
		font-size:13px;
		margin-left:10px;
		border:1px solid #E7E7E7;
		line-height: 18px;
	}
	#order-list .order-item .order-info{
		margin-top: -6px;
		background-color:#f8f8f8;
	}
	#order-list .order-item .order-nt{
		background-color:#f8f8f8;
	}
	#order-list .order-item .pay-status{
		position:absolute;
		left:50%;
		margin-left:-50px;
		bottom:170px;
	}
	#order-list .order-item .pay-status.have-pay{
		width:100px;
		height:50px;
		background:url("http://1.duoxunwl.com/addons/we7_wmall/static/img/have-pay.png") no-repeat center center;
		background-size:100% 100%;
	}
	#order-list .order-item .pay-status.delivery-pay{
		width: 110px;
		height: 70px;
		background:url("http://1.duoxunwl.com/addons/we7_wmall/static/img/delivery-pay.png") no-repeat center center;
		background-size:100% 100%;
	}
	#order-list .order-item .order-type{
		padding: 5px 8px;
		color: #fff;
		text-align: center;
		border-radius: 4px;
	}
	#order-list .icon-scan, #order-list .icon-circle{
		font-size: 21px;
	}
	/*接单订单倒计时*/
	#order-list .cancel-count-down{
		padding: 10px;
		color: #33aafc;
		background-color: rgba(51, 170, 252, 0.15);
	}
</style>


